header {
  /* background-color: rgb(29, 71, 126);
   */
  background-color: rgb(32, 46, 68);
  height: 60px;
  padding: 10px 40px;
  color: white;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  text-align: center;
  z-index: 2;
}

.header-title {
  font-weight: 400;
  font-size: 50px;
  float: left;
  margin-top: 0px;
  color: white;
}

/* +++++++++++ Nav ++++++++++++++++++++++++++++++++++++++++++++++++++++++ */
nav {
  /* overflow: hidden; */
  background-color: rgb(32, 46, 68);
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}

nav a {
  color: #FFF;
}

nav a:visited {
  color: #FFF;
}

nav .navWide {
  display: none;
  margin: 0 auto;
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}

nav .navWide .wideDiv {
  text-align: end;
}

nav .navWide .wideDiv a {
  text-decoration: none;
  display: inline-block;
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
  margin: 5px 20px 0 0;
  transition: 0.8s;
}

nav .navWide .wideDiv a:hover {
  color: yellow;
  transition: 0.8;
  -webkit-transition: 0.8;
  -moz-transition: 0.8;
  -ms-transition: 0.8;
  -o-transition: 0.8;
}

nav .navNarrow i {
  float: right;
  cursor: pointer;
  color: #FFF;
  margin-right: 100px;
}

nav .navNarrow .narrowLinks {
  display: none;
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}

nav .navNarrow .narrowLinks a {
  text-decoration: none;
  display: block;
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
  float: right;
  clear: both;
  margin: 10px 100px 30px 0;
  background-color: rgb(32, 46, 68);
  height: 200%;
  width: 50%;
}

/* Media Queries ++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */



@media only screen and (min-width: 440px) {
  nav .navWide {
    display: block;
    -webkit-transition: 0.8s;
    -o-transition: 0.8s;
    transition: 0.8s;
    background-color: rgb(32, 46, 68);
    padding: 10px;
    float: right;
    margin-right: 100px;
    font-size: 30px;
  }

  nav .navNarrow {
    display: none;
    -webkit-transition: 0.8s;
    -o-transition: 0.8s;
    transition: 0.8s;
  }

  nav .narrowLinks {
    text-decoration: none;
    display: block;
    -webkit-transition: 0.8s;
    -o-transition: 0.8s;
    transition: 0.8s;
    float: right;
    clear: left;
    padding: 0.5em 0;
    background-color: rgb(32, 46, 68);
    height: 200%;
  }


  nav .navNarrow i {
    margin-right: 70px;
  }

}

@media only screen and (max-width: 690px) {


  .header-title {
    margin-left: -30px;
  }

  nav .navWide {
    margin-right: 50px;
  }

  nav .wideDiv a {
    margin: 5px 0 0 10px;
  }

}


@media only screen and (max-width: 600px) {

  header {
    height: 45px;
    margin: 0;
  }

  .header-title {
    font-size: 38px;
    margin-left: -30px;
    margin-top: -5px;
    text-align: start;
    padding-bottom: 15px;
  }

  nav {
    margin-right: -20px;
  }

  nav .navWide {
    margin-top: 0;
    padding: 0;
    font-size: 25px;
  }

  nav .navNarrow i {
    margin-right: 100px;
    margin-top: 7px;
  }

  nav .narrowLinks {
    margin-top: -7px;
    margin-right: -25px;
    padding: 20px;
  }

  nav .navNarrow .narrowLinks a {
    width: 100%;
    margin-right: 55px;
    font-size: 28px;
    margin-top: -35px;
    padding: 5%;
  }


}

@media only screen and (max-width: 350px) {
  .navNarrow {
    margin-right: -30px;
  }

  .header-title {
    font-size: 220%;
    margin-left: -35px;
  }
}





